<template>
  <div>
    <div v-if="facetMinimized" class="location-facet-minimized">
      <h3>Locations</h3>
      <div class="facet-controls-minimized">
        <span @click="minimizeFacet" class="control-button maximize-button"></span>
      </div>
    </div>
    <div v-if="!facetMinimized" class="location-facet">
      <div class="facet-controls">
        <span
          @click="facetCollapsed = !facetCollapsed"
          class="control-button"
          :class="facetCollapsed ? 'full-button': 'collapse-button'"
        ></span>
        <span
          @click="minimizeFacet"
          class="control-button"
          :class="facetMinimized ? 'maximize-button': 'minimize-button'"
        ></span>
      </div>
      <h3>Locations</h3>
      <div v-if="!facetCollapsed" class="facet-container">
        <input type="text" class="input-filter" placeholder="Filter locations" v-model="search" />
        <ul class="letter-index">
          <li v-for="letter in initialLetters">
            <a class="letter-anchor" :href="`#${letter.code}`">{{ letter.initialLetter }}</a>
          </li>
        </ul>
        <p class="clear-all-button" @click="clearSelectedCountries">X Clear All</p>
        <LocationsList v-if="!facetCollapsed" :countries="filteredItems" />
      </div>
    </div>
  </div>
</template>
<script>
import LocationsList from "./LocationsList.vue";
export default {
  components: {
    LocationsList
  },
  props: {
    countries: [Array, Object]
  },
  data() {
    return {
      search: "",
      facetCollapsed: false,
      facetMinimized: false
    };
  },
  methods: {
    clearSelectedCountries() {
      this.$store.dispatch("clearAll");
      this.search = "";
    },
    collapseFacet() {
      this.facetCollapsed = this.facetCollapsed === true ? false : true;
    },
    minimizeFacet() {
      this.facetMinimized = this.facetMinimized === true ? false : true;
    }
  },
  computed: {
    filteredItems() {
      return this.countries.filter(country => {
        return country.name.toLowerCase().includes(this.search.toLowerCase());
      });
    },
    initialLetters() {
      let lettersHash = {};
      let lettersArray = [];
      for (var index in this.filteredItems) {
        let firstLetter = this.filteredItems[index].name.charAt(0);
        if (!lettersHash[firstLetter]) {
          lettersHash[firstLetter] = this.filteredItems[index].name;
          lettersArray.push({
            initialLetter: firstLetter,
            firstValue: this.filteredItems[index].name,
            code: this.filteredItems[index].code
          });
        }
        continue;
      }
      return lettersArray;
    }
  }
};
</script>
