<template>
  <li class="country" :id="countryData.code">
    <input type="checkbox" v-model="countryData.selected" />
    <span :class="lowerCaseCode"></span>
    <span class="country-name">{{ countryData.name }}</span>
  </li>
</template>
<script>
export default {
  props: {
    countryData: Object
  },
  computed: {
    lowerCaseCode() {
      let lowerCountryCode;
      lowerCountryCode = this.countryData.code.toLowerCase();
      return `flag-icon flag-icon-${lowerCountryCode}`;
    }
  }
};
</script>
<style scoped>
.country {
  text-align: left;
}
.country input {
  margin-right: 8px;
}
.country-name {
  margin-left: 8px;
  overflow: hidden;
  width: 96px;
  display: inline-block;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
}
</style>